/* Styling for Navbar ends here */
.username-form {
	padding-top: 20vh;
	color: white;
}
.username-button {
	margin-top: 20px;
}
.loader {
  border: 7px solid #3a343d; /* Light grey */
  border-top: 7px solid #61A8D6; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  text-align: center;
  margin: 24% auto;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.rules {
	color: white;
}
/* Jumbotron CSS Rules */
img {
	width: 500px;
	height: 500px;
	float: right;
}
.jumbotron {
	font-family: 'Raleway';
	text-align: left;
	margin: 100px 150px 100px 100px;
}
.jumbotron-heading {
	padding-top: 120px;
	color: white;
	font-size: 50px;
	margin-bottom: 10px;
}
.jumbotron-subtitle {
	color: white;
	font-size: 25px;
	margin-top: 0px;
}
